@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
  background-color: #fcf3f6;
}

h1 {
  font-size: 2.5rem;
  color: #2c292b;
}

h2 {
  font-size: 1.5rem;
  color: #2c292b;
}

.MainNavigation_header__pcWBd {
    width: 100%;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #77002e;
    padding: 0 10%;
  }
  
  .MainNavigation_logo__3mTft {
    font-size: 2rem;
    color: white;
    font-weight: bold;
  }
  
  .MainNavigation_header__pcWBd ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: baseline;
  }
  
  .MainNavigation_header__pcWBd li {
    margin-left: 3rem;
  }
  
  .MainNavigation_header__pcWBd a {
    text-decoration: none;
    font-size: 1.5rem;
    color: #fcb8d2;
  }
  
  .MainNavigation_header__pcWBd a:hover,
  .MainNavigation_header__pcWBd a:active,
  .MainNavigation_header__pcWBd a.MainNavigation_active__oN3Ka {
    color: white;
  }
  
  .MainNavigation_badge__QYsXO {
    background-color: #cc2062;
    color: white;
    border-radius: 12px;
    padding: 0 1rem;
    margin-left: 0.5rem;
  }

.Layout_main__auk_r {
    margin: 3rem auto;
    width: 90%;
    max-width: 40rem;
  }
